.file-loading {
	top: 0;
	right: 0;
	width: 25px;
	height: 25px;
	font-size: 999px;
	text-align: right;
	color: #fff;
	background: transparent url('../img/loading.gif') top left no-repeat;
	border: none;
}

.file-object {
	margin: 0 0 -5px 0;
	padding: 0;
}

.btn-file {
	position: relative;
	overflow: hidden;
}

.btn-file input[type=file] {
	position: absolute;
	top: 0;
	right: 0;
	min-width: 100%;
	min-height: 100%;
	text-align: right;
	opacity: 0;
	background: none repeat scroll 0 0 transparent;
	cursor: inherit;
	display: block;
}

.file-caption-name {
	display: inline-block;
	overflow: hidden;
	height: 20px;
	word-break: break-all;
}

.input-group-lg .file-caption-name {
	height: 25px;
}

.file-preview-detail-modal {
	text-align: left;
}

.file-error-message {
	background-color: #f2dede;
	color: #a94442;
	text-align: center;
	border-radius: 5px;
	padding: 5px 10px 5px 5px;
}

.file-error-message pre,
.file-error-message ul {
	margin: 5px 0;
	text-align: left;
}

.file-caption-disabled {
	background-color: #EEEEEE;
	cursor: not-allowed;
	opacity: 1;
}

.file-preview {
	border-radius: 5px;
	border: 1px solid #ddd;
	padding: 5px;
	width: 100%;
	margin-bottom: 5px;
}

.file-preview-frame {
	display: table;
	margin: 8px;
	height: 160px;
	border: 1px solid #ddd;
	box-shadow: 1px 1px 5px 0 #a2958a;
	padding: 6px;
	float: left;
	text-align: center;
	vertical-align: middle;
}

.file-preview-frame:not(.file-preview-error):hover {
	box-shadow: 3px 3px 5px 0 #333;
}

.file-preview-image {
	height: 160px;
	vertical-align: middle;
}

.file-preview-text {
	text-align: left;
	width: 160px;
	margin-bottom: 2px;
	color: #428bca;
	background: #fff;
	overflow-x: hidden;
}

.file-preview-other {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
	width: 160px;
	height: 160px;
	border: 2px solid #999;
	border-radius: 30px;
	opacity: 0.8;
}

.file-actions,
.file-other-error {
	text-align: left;
}

.file-icon-lg {
	font-size: 1.2em;
}

.file-icon-2x {
	font-size: 2.4em;
}

.file-icon-4x {
	font-size: 4.8em;
}

.file-input-new .file-preview,
.file-input-new .close,
.file-input-new .glyphicon-file,
.file-input-new .fileinput-remove-button,
.file-input-new .fileinput-upload-button,
.file-input-ajax-new .fileinput-remove-button,
.file-input-ajax-new .fileinput-upload-button {
	display: none;
}

.file-thumb-loading {
	background: transparent url('../img/loading.gif') no-repeat scroll center center content-box !important;
}

.file-actions {
	margin-top: 15px;
}

.file-footer-buttons {
	float: right;
}

.file-upload-indicator {
	padding-top: 2px;
	cursor: default;
	opacity: 0.8;
	width: 60%;
}

.file-upload-indicator:hover {
	font-weight: bold;
	opacity: 1;
}

.file-footer-caption {
	display: block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	width: 160px;
	text-align: center;
	padding-top: 4px;
	font-size: 11px;
	color: #777;
	margin: 5px auto 10px auto;
}

.file-preview-error {
	opacity: 0.65;
	box-shadow: none;
}

.file-preview-frame:not(.file-preview-error) .file-footer-caption:hover {
	color: #000;
}

.file-drop-zone {
	border: 1px dashed #aaa;
	border-radius: 4px;
	height: 100%;
	text-align: center;
	vertical-align: middle;
	margin: 12px 15px 12px 12px;
	padding: 5px;
}

.file-drop-zone-title {
	color: #aaa;
	font-size: 40px;
	padding: 85px 10px;
}

.file-highlighted {
	border: 2px dashed #999 !important;
	background-color: #f0f0f0;
}

.file-uploading {
	background: url('../img/loading-sm.gif') no-repeat center bottom 10px;
	opacity: 0.65;
}

.file-error-message .close {
	margin-top: 5px;
}

.file-thumb-progress .progress,
.file-thumb-progress .progress-bar {
	height: 10px;
	font-size: 9px;
	line-height: 10px;
}

.file-thumbnail-footer {
	position: relative;
}

.file-thumb-progress {
	position: absolute;
	top: 22px;
	left: 0;
	right: 0;
}

/* IE 10 fix */

.btn-file::-ms-browse {
	width: 100%;
	height: 100%;
}

.image-preview-input {
	position: relative;
	overflow: hidden;
	margin: 0px;
	color: #333;
	background-color: #fff;
	border-color: #ccc;
}

.image-preview-input input[type=file] {
	position: absolute;
	top: 0;
	right: 0;
	margin: 0;
	padding: 0;
	font-size: 20px;
	cursor: pointer;
	opacity: 0;
	filter: alpha(opacity=0);
}

.image-preview-input-title {
	margin-left: 2px;
}

